.active{
    background-color: #1d4ed8;
}

nav{
    display: flex;
    align-items: center;
    background-color: #4c6693;
    position: sticky;
    justify-content: space-between;
    top: 0;
}
nav .title{
    font-size: 1.5rem;
    margin: 1rem;
    font-weight: bold;
    color: #ffff;
    text-decoration: none;
}
nav ul{
    display: flex;
}
nav ul li{
list-style: none;
}
nav ul .icons{
    display: flex;
}
nav ul li a{
    display: block;
    text-decoration: none;
    color: white;
    padding: 0.5rem;
    margin: 0 0.5rem;
    border-radius: 0.5rem;
}
nav ul li a:not(.active):hover{
    
    background-color: #172554;
}
nav .menu{
    display: none;
    position: absolute;
    top: 0.75rem;
    right: 0.5rem;
    flex-direction: column;
    justify-content: space-between;
    width: 2.25rem;
    height: 2rem;
}
nav .menu span{
    height: o.4rem;
    width: 100%;
    background-color: #fff;
    border-radius: 0.2rem;
}
@media (max-width: 480px){
    nav .menu{
        display: flex;
    }
    nav{
        flex-direction: column;
        align-items: start;
    }
    nav ul{
        display: none;
        flex-direction: column;
        width: 100%;
        margin-bottom: 0.25rem;
    }
    nav ul.open{
        display: flex;

    }
    nav ul li{
        width: 100%;
        text-align: center;
    }
    nav ul li a{
        margin: 0.2rem 0.5rem;
    }
}

